.product-item {
	display: block;
	margin-bottom: 20px;
	padding: 10px;
	color: $main;
	@include transition(all,0.3s);
	.img {
		height: 180px;
		@include flexbox;
		align-items: center;
		align-content: center;
		justify-content: center;
		margin-bottom: 10px;
		img {
			width: auto;
			height: 100%;
			max-height: 100%; } }
	.caption {
		.name {
			@include fs(14rem);
			@include textover(2); }
		.author {
			@include fs(11rem);
			color: darken($white,40%);
			margin-bottom: 10px; }
		.price {
			@include flexbox;
			@include pos(rel,null);
			align-items: flex-end;
			align-content: flex-end;
			margin-bottom: 10px;
			.promotion {
				@include pos(abs, 0 0 null null);
				@include fs(11rem);
				font-weight: 700;
				background: $orange;
				color: $white;
				line-height: 16px;
				padding: 0px 3px;
				@include bor-rad(0 4px 4px 0);
				&:before {
					content: '';
					display: block;
					border: 8px solid transparent;
					border-left: none;
					border-right-color: $orange;
					@include pos(abs, 0 null null -8px); } }
			.new {
				@include fs(14rem);
				color: #f00;
				font-weight: 500;
				line-height: 1;
				margin-right: 10px; }
			.old {
				@include fs(11rem);
				color: darken($white,40%);
				line-height: 1; } }
		.rating {
			@include flexbox;
			align-items: center;
			align-content: center;
			@include fs(11rem);
			color: darken($white,40%);
			ul {
				@include flexbox;
				align-items: center;
				align-content: center;
				margin-right: 10px;
				li {
					color: darken($white,20%);
					+ li {
						margin-left: 3px; }
					&.active {
						.fa {
							color: $amber; } } } } } }
	&:hover {
		color: $green; } }
