.breadcrumb-section {
	ol.breadcrumb {
		margin: 0;
		padding: 0;
		background: none;
		@include flexbox;
		align-items: center;
		align-content: center;
		flex-wrap: wrap;
		min-height: 40px;
		li {
			color: darken($white,60%);
			&:after {
				content: '\f105';
				margin: 0px 10px; }
			a {
				@include fs(12rem);
				color: darken($white,60%);
				&:hover {
					color: $green; } }
			&:last-child {
				&:after {
					display: none; }
				a {
					color: $green; } } } } }
