// FORM INIT
.form-wrap {
	.form-group {
		margin-bottom: 10px; }
	label {
		display: block;
		@include fs(13rem);
		color: $main;
		// font-weight: 500
		padding: 5px 0; }
	input, select, textarea {
		@include fs(13rem);
		padding: 7px 15px;
		@include bor-rad(3px);
		border: 1px solid darken($white,10%);
		width: 100%;
		color: $main;
		@include transition(all,0.5s);
		&::-webkit-input-placeholder {
			color: inherit;
			opacity: 0.5; }
		&:active, &:focus {
			border-color: lighten($green,20%);
			box-shadow: 0 0 0 0.2rem color-rgba($green,0.1); } }
	ul.gender {
		@include flexbox;
		align-items: center;
		align-content: center;
		li {
			label {
				width: auto; }
			+ li {
				margin-left: 30px; } } }
	select {
		padding: 8px 15px; } }

// FORM COMPONENTS
.input-number {
	@include flexbox;
	border: 1px solid darken($white,5%);
	@include bor-rad(5px);
	background: $white;
	width: 72px;
	.btn-spin {
		width: 20px;
		height: 22px;
		@include fs(14rem);
		line-height: 22px;
		text-align: center;
		font-weight: 700;
		cursor: pointer; }
	input {
		width: 30px;
		margin: 0;
		padding: 0;
		border: none;
		line-height: 22px;
		text-align: center;
		@include fs(13rem);
		font-weight: 700; } }

.checkbox {
	input {
		display: none;
		+ label {
			color: $main;
			padding: 0 0 0 20px;
			@include pos(rel,null);
			@include fs(13rem);
			&:hover {
				color: $main; }
			&:before {
				content: '';
				width: 14px;
				height: 14px;
				@include bor-rad(3px);
				border: 1px solid darken($white,10%);
				@include pos(abs, 3px null null 0); }
			&:after {
				content: '\f00c';
				width: 14px;
				height: 14px;
				@include flexbox;
				align-items: center;
				align-content: center;
				justify-content: center;
				@include fs(11rem);
				@include bor-rad(3px);
				color: $white;
				background: $green;
				line-height: 1;
				@include pos(abs, 3px null null 0);
				z-index: 1;
				opacity: 0; } }
		&:checked {
			+ label {
				&:after {
					opacity: 1; } } } } }
.radio {
	input {
		display: none;
		+ label {
			color: $main;
			padding: 0 0 0 20px;
			@include pos(rel,null);
			@include fs(13rem);
			&:hover {
				color: $main; }
			&:before {
				content: '';
				width: 14px;
				height: 14px;
				@include bor-rad(50px);
				border: 1px solid darken($white,20%);
				@include pos(abs, 3px null null 0); }
			&:after {
				content: '';
				width: 8px;
				height: 8px;
				@include bor-rad(50px);
				background: $green;
				@include pos(abs, 6px null null 3px);
				z-index: 1;
				opacity: 0; } }
		&:checked {
			+ label {
				&:after {
					opacity: 1; } } } } }
.file {
	input[type="file"] {
		display: none; }
	label {
		&:hover {
			color: $black; } } }
