.cart-page {
	padding-top: 10px;
	.row {
		margin: 0px -5px;
		.col-12 {
			padding: 0px 5px; } }
	.cart-title {
		@include fs(14rem);
		text-transform: uppercase;
		font-weight: 500;
		padding: 10px 0;
		span {
			color: darken($white,40%);
			text-transform: none;
			font-weight: normal; } } }
.cart-step {
	background: $white;
	margin-bottom: 10px;
	&.s2 {
		padding: 0 10px; }
	.cart-table {
		background: $white;
		table {
			th {
				border-bottom: 1px solid darken($white,5%);
				@include fs(14rem);
				font-weight: 700;
				line-height: 20px;
				padding: 10px 15px;
				text-align: center;
				&:first-child {
					text-align: left; } }
			td {
				text-align: center;
				vertical-align: middle;
				&:first-child {
					text-align: left; } }
			.product {
				padding: 15px;
				width: 250px;
				@include flexbox;
				align-items: center;
				align-content: center;
				.img {
					width: 70px; }
				.caption {
					padding-left: 20px;
					.name {
						display: block;
						@include fs(13rem);
						line-height: 1.3;
						&:hover {
							color: $green; } } } }
			.price, .sale, .total {
				@include fs(13rem);
				text-align: center; }
			.btn-remove {
				@include fs(16rem);
				color: darken($white,40%);
				text-align: center;
				display: block;
				&:hover {
					color: #f00; } } } }
	.cart-service {
		padding: 10px 0;
		border-bottom: 1px solid darken($white,5%); }
	.cart-title-info {
		@include fs(16rem);
		line-height: 20px;
		padding: 10px 10px;
		@include pos(rel,null);
		border-bottom: 1px solid darken($white,5%);
		margin: 0px -10px;
		background: #FAFAFA;
		.btn-edit {
			@include pos(abs, 10px 10px null null);
			color: $green; } }
	.cart-address {
		padding: 10px;
		li {
			@include fs(13rem);
			margin-bottom: 10px; } }
	.cart-product {
		border-bottom: 1px solid darken($white,5%);
		.title {
			line-height: 20px;
			padding: 10px 0;
			@include fs(13rem);
			font-weight: 700;
			border-bottom: 1px solid darken($white,5%); }
		.item {
			@include flexbox;
			align-items: center;
			align-content: center;
			padding: 10px 0;
			.img {
				display: block;
				width: 47px; }
			.caption {
				width: calc(100% - 47px - 10px);
				padding-left: 10px;
				.name {
					display: block;
					@include fs(13rem);
					line-height: 1.3;
					color: $green;
					@include textover(2); }
				.amount {
					@include fs(13rem);
					line-height: 1.3;
					font-weight: 700;
					color: $main; } }
			.price {
				width: 100px;
				text-align: right;
				@include fs(13rem);
				font-weight: 700;
				color: #f00; } } }
	.cart-overview {
		padding: 10px 0;
		border-bottom: 1px solid darken($white,5%);
		ul {
			li {
				@include flexbox;
				align-items: center;
				align-content: center;
				justify-content: space-between;
				@include fs(13rem);
				margin-bottom: 5px;
				label {}
				.value {} } } }
	.cart-total {
		@include flexbox;
		align-items: flex-start;
		align-content: flex-start;
		justify-content: space-between;
		padding: 10px 0;
		label {
			@include fs(13rem);
			text-transform: uppercase;
			font-weight: 700; }
		.value {
			text-align: right; }
		strong {
			color: #f00;
			@include fs(18rem);
			line-height: 1;
			font-weight: 700;
			display: block; }
		span {
			@include fs(13rem);
			color: darken($white,40%); } }
	.cart-des {
		@include fs(13rem);
		line-height: 1.5;
		color: $main;
		padding-bottom: 10px; }
	.btn-wrap {
		padding-bottom: 10px; }
	.btn-continue {
		@extend .button-primary;
		line-height: 40px;
		@include fs(14rem);
		text-transform: uppercase;
		text-align: center;
		display: block; }
	.cart-gift {
		label {
			display: block;
			width: 100%;
			padding: 10px;
			line-height: 20px;
			@include fs(13rem);
			border-bottom: 1px solid darken($white,5%); }
		.code-input {
			@include pos(rel,null);
			padding: 10px;
			input {
				line-height: 34px;
				border: 1px solid darken($white,5%);
				@include bor-rad(4px);
				@include fs(13rem);
				width: 100%;
				padding: 0px 15px; }
			.code-submit {
				@extend .button-primary;
				min-width: unset;
				width: 70px;
				@include fs(13rem);
				font-weight: normal;
				text-transform: none;
				line-height: 36px;
				@include pos(abs, 10px 10px null null); } } } }

// progress

.cart-progress {
	padding: 15px 0;
	margin-bottom: 10px;
	ul {
		@include flexbox;
		align-items: center;
		align-content: center;
		justify-content: center;
		li {
			width: 25%;
			@include flexbox;
			flex-direction: column;
			align-items: center;
			align-content: center;
			@include pos(rel,null);
			&:before {
				content: '';
				display: block;
				width: 100%;
				height: 10px;
				background: #E1E1E1;
				@include pos(abs, 25px 50% null null);
				@include transform(translateY(-50%)); }
			&:first-child {
				&:before {
					display: none; } }
			.number {
				width: 40px;
				height: 40px;
				@include bor-rad(50px);
				background: #E1E1E1;
				@include flexbox;
				align-items: center;
				align-content: center;
				justify-content: center;
				color: #888888;
				@include fs(16rem);
				border: 5px solid #EFEEF3;
				box-sizing: content-box;
				margin-bottom: 10px;
				@include pos(rel,null);
				z-index: 3; }
			label {
				color: #888888;
				@include fs(14rem); }
			&.active {
				&:before {
					background: $green; }
				.number {
					background: $green;
					color: $white; }
				label {
					font-weight: 700;
					color: $main; } } } } }

.cart-step.s4 {
	padding: 0px 20px;
	margin-bottom: 40px;
	.cart-login {
		padding: 30px 0;
		width: 100%;
		.form-title {
			@include fs(14rem);
			font-weight: 700;
			text-transform: uppercase;
			margin-bottom: 10px; }
		.form-hr {
			width: 100%;
			height: 1px;
			background: darken($white,5%);
			max-width: 700px;
			margin: 20px auto; }
		.form-wrap {
			max-width: 600px;
			margin: 0 auto;
			.form-group {
				@include flexbox;
				flex-wrap: wrap;
				@include flexbox;
				align-items: center;
				align-content: center;
				label {
					width: 120px;
					text-align: right;
					padding-right: 10px; }
				input {
					width: calc(100% - 120px); }
				select {
					max-width: 230px; } } }

		.login-choice {
			margin-left: 120px;
			margin-bottom: 15px;
			li {
				margin-bottom: 10px;
				&:last-child {
					margin-bottom: 0; } } }
		.forget-pw {
			margin-left: 120px;
			@include fs(13rem);
			color: $green;
			margin-top: 10px;
			&:hover {
				color: $orange; } }
		.agree {
			@extend .checkbox;
			margin-left: 120px;
			margin-bottom: 15px; }
		.btn-login {
			@extend .button-primary;
			text-transform: uppercase;
			// min-width: 400px
			// line-height: 50px
			// +fs(16rem)
			margin-left: 120px; }
		.form-des {
			@include fs(13rem);
			color: darken($white,40%);
			margin-left: 120px; } } }

