footer {
	background: $white;
	.footer-top {
		background: $green;
		padding: 15px 0px 0px; }
	.footer-feature {
		.feature-col {
			color: $white;
			margin-bottom: 15px;
			@include flexbox;
			.icon {
				width: 50px;
				.md {
					@include fs(36rem); } }
			.caption {
				width: calc(100% - 50px);
				color: $white;
				padding-top: 5px;
				.title {
					@include fs(14rem);
					font-weight: 700;
					text-transform: uppercase;
					margin-bottom: 5px; }
				.des {
					@include fs(13rem);
					opacity: 0.8; } } } }
	.footer-main {
		padding: 40px 0 20px; }
	.footer-col {
		margin-bottom: 40px;
		ul {
			li {
				@include fs(13rem);
				padding-left: 20px;
				@include pos(rel,null);
				.md {
					@include fs(16rem);
					@include pos(abs, 0 null null 0);
					vertical-align: middle; } } } }
	.footer-title {
		@include fs(14rem);
		font-weight: 700;
		text-transform: uppercase;
		margin-bottom: 10px;
		span {
			text-transform: none; } }
	.footer-link {
		a {
			display: block;
			@include fs(13rem);
			&:hover {
				color: $green; } } }
	.footer-about {
		border-top: 1px solid darken($white,10%);
		padding-top: 40px; }
	.footer-bottom {
		background: #4a4a4a;
		.footer-wrap {
			@include flexbox;
			justify-content: space-between;
			align-items: center;
			align-content: center;
			min-height: 45px; }
		.copyright {
			@include fs(13rem);
			color: $white; }
		.col-right {
			justify-content: flex-end; }
		.social {
			@include flexbox;
			align-items: center;
			align-content: center;
			a {
				@include flexbox;
				align-items: center;
				align-content: center;
				justify-content: center;
				color: $main;
				background: $white;
				@include bor-rad(50px);
				width: 24px;
				height: 24px;
				+ a {
					margin-left: 10px; } } } } }
