.webbook-detail {
	padding: 20px;
	.webbook-info {
		margin-bottom: 15px; }
	.webbook-name {
		@include fs(24rem);
		font-weight: 700; }
	time {
		display: inline-block;
		vertical-align: middle;
		@include fs(12rem);
		color: color-rgba($black,0.5); }
	.category {
		display: inline-block;
		vertical-align: middle;
		@include fs(12rem);
		line-height: 1;
		color: $white;
		background: $red;
		padding: 3px 5px;
		margin-left: 10px;
		text-transform: capitalize; }
	.content {
		margin-bottom: 20px;
		.content-product {
			text-align: center;
			.btn-buy {
				display: inline-block;
				min-width: 225px;
				text-align: center;
				line-height: 40px;
				color: $white;
				background: linear-gradient($orangelight,$orange);
				@include bor-rad(4px);
				@include fs(14rem);
				font-weight: 700;
				&:hover {
					box-shadow: 0px 10px 20px -10px $orange; } } } }
	.webbook-product {
		padding: 20px;
		border: 1px solid darken($white,10%);
		@include flexbox;
		.img {
			width: 130px; }
		.caption {
			width: calc(100% - 130px);
			padding-left: 20px;
			.name {
				@include fs(24rem);
				color: $main;
				@include textover(2); }
			.rating {
				@include flexbox;
				align-items: center;
				align-content: center;
				@include fs(14rem);
				ul {
					@include flexbox;
					align-items: center;
					align-content: center;
					margin-right: 10px;
					li {
						color: darken($white,20%);
						+ li {
							margin-left: 3px; }
						&.active {
							.fa {
								color: $amber; } } } } }
			.des {
				margin-bottom: 15px;
				ul {
					li {
						@include flexbox;
						align-items: flex-end;
						align-content: flex-end;
						@include fs(13rem);
						height: 1.7rem;
						span {
							margin-left: 10px; } }
					.price-old {
						color: darken($white,40%); }
					.price-new {
						@include fs(28rem);
						font-weight: 700;
						color: #FF0000;
						line-height: 1; } } }
			.btn-buy {
				display: inline-block;
				min-width: 225px;
				text-align: center;
				line-height: 40px;
				color: $white;
				background: linear-gradient($orangelight,$orange);
				@include bor-rad(4px);
				@include fs(14rem);
				font-weight: 700;
				&:hover {
					box-shadow: 0px 10px 20px -10px $orange; } } } } }
.webbook-other {
	padding: 20px;
	.other-list {}
	.other-item {
		.img {
			margin-bottom: 15px; }
		.caption {}
		.name {
			@include fs(16rem);
			font-weight: 700;
			line-height: 1.3;
			a {
				@include textover(2); } }
		time {
			display: inline-block;
			vertical-align: middle;
			@include fs(11rem);
			color: color-rgba($black,0.5); }
		.category {
			display: inline-block;
			vertical-align: middle;
			@include fs(11rem);
			line-height: 1;
			color: $white;
			background: $red;
			padding: 3px 5px;
			margin-left: 10px;
			text-transform: capitalize; } } }
